<template>
  <div class="mind-map-app">
    <Header />
    <v-container
      class="pa-4 pa-sm-2"
      fluid
      style="max-width: 1800px; margin: auto"
    >
      <!-- PDF ‰∏äÂÇ≥ÂçÄÂ°ä -->
      <v-row>
        <v-col cols="12">
          <section
            class="pdf-upload-section pa-md-12 pa-6 rounded-lg elevation-1"
          >
            <h2 class="mb-4 text-h4 text-md-h4 text-sm-h5 text-xs-h6">
              ‰∏äÂÇ≥ PDF Ê™îÊ°à
            </h2>
            <div class="upload-guide mb-4">
              <p>
                Ê≥ïÊñáÂè≤Êñô‰∏äÂÇ≥ÔºåÁ≥ªÁµ±ÁöÑÈõôÂ±§AI (Gemini 1.5 ProÊ†∏ÂøÉ)
                Âç≥ÊôÇÁîüÊàê‰∫íÂãïÂøÉÊô∫Âúñ„ÄÇË§áÈõúÊ≠∑Âè≤Ë¶ñË¶∫ÂåñÔºåÂä©ÊÇ®È´òÊïàÁêÜËß£„ÄÅËºïÈ¨ÜË®òÊÜ∂ÔºÅ
              </p>
            </div>
            <form @submit.prevent="uploadFile" class="upload-form">
              <input
                type="file"
                id="pdfFile"
                accept=".pdf"
                ref="fileInput"
                required
                @change="handleFileChange"
                style="display: none"
              />
              <div class="button-container">
                <v-btn
                  color="primary"
                  @click="$refs.fileInput.click()"
                  class="upload-btn"
                >
                  {{ fileName || "ÈªûÊìäÈÅ∏ÊìáÊ™îÊ°à" }}
                </v-btn>
                <v-btn
                  type="submit"
                  color="success"
                  class="ml-md-3 mt-2 mt-md-0 upload-btn"
                  :disabled="processing"
                  >‰∏äÂÇ≥</v-btn
                >
              </div>
            </form>
            <div v-if="processing" class="mt-4">
              <div class="process-status">
                <div class="process-text">{{ processStage }}</div>
                <v-progress-linear
                  color="primary"
                  height="20"
                  :value="progress"
                  striped
                  buffer-value="0"
                >
                  <template v-slot:default>
                    {{ Math.ceil(progress) }}%
                  </template>
                </v-progress-linear>
              </div>
            </div>
            <div v-html="resultMessage" class="mt-4"></div>
          </section>
        </v-col>
      </v-row>
      <!-- ÂøÉÊô∫ÂúñËàáÂø´Êç∑Èçµ‰∏¶Êéí -->
      <v-row class="mt-8 mind-map-row" dense>
        <!-- Â∑¶ÂÅ¥ÂøÉÊô∫Âúñ -->
        <v-col cols="12" md="9" sm="12" class="px-md-4 px-2 d-flex flex-column">
          <section
            class="mindmap-section pa-md-4 pa-2 elevation-2 rounded flex-grow-1"
          >
            <h2 class="mb-3">ÊàëÁöÑÂøÉÊô∫Âúñ</h2>
            <div id="map" ref="map" class="mind-map-container"></div>
            <div class="export-buttons mt-4">
              <v-btn
                color="success"
                class="me-2 mb-2 mb-md-0 export-btn"
                @click="exportPng"
                ><span class="d-none d-sm-block">ÂåØÂá∫ PNG Ê™îÊ°à</span
                ><span class="d-block d-sm-none">PNG</span></v-btn
              >
              <v-btn
                color="info"
                class="me-2 mb-2 mb-md-0 export-btn"
                @click="exportSvg"
                ><span class="d-none d-sm-block">ÂåØÂá∫ SVG ÂêëÈáèÂúñ</span
                ><span class="d-block d-sm-none">SVG</span></v-btn
              >
              <v-btn
                color="warning"
                class="me-2 mb-2 mb-md-0 export-btn"
                @click="exportJson"
                ><span class="d-none d-sm-block">ÂåØÂá∫ JSON ÁµêÊßã</span
                ><span class="d-block d-sm-none">JSON</span></v-btn
              >
              <v-spacer class="d-none d-md-block"></v-spacer>
              <v-btn
                color="primary"
                class="export-btn quiz-btn"
                @click="generateQuiz"
              >
                <span class="d-none d-sm-block">ÂÑ≤Â≠òÂæåÁîüÊàêÊ∏¨È©ó</span>
                <span class="d-block d-sm-none">ÁîüÊàêÊ∏¨È©ó</span>
              </v-btn>
            </div>
          </section>
        </v-col>

        <!-- Âè≥ÂÅ¥Âø´Êç∑Èçµ -->
        <v-col
          cols="12"
          md="3"
          sm="12"
          class="px-md-4 px-2 mt-4 mt-md-0 d-flex flex-column"
        >
          <section
            class="shortcut-section pa-md-4 pa-3 elevation-2 rounded flex-grow-1"
          >
            <h2 class="mb-3">Âø´Êç∑ÈçµË™™Êòé</h2>
            <div class="shortcut-scroll">
              <table class="shortcut-table">
                <tbody>
                  <tr v-for="(item, index) in shortcuts" :key="index">
                    <td class="shortcut-key">{{ item.key }}</td>
                    <td class="shortcut-func">{{ item.function }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </section>
        </v-col>
      </v-row>
    </v-container>

    <Footer />
  </div>
</template>

<script>
import Header from "./AppHeader.vue";
import Footer from "./AppFooter.vue";
import MindElixir from "mind-elixir";
import axios from "axios";

export default {
  name: "MindMapUploader",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      mind: null,
      resultMessage: "",
      fileName: "",
      backendUrl: "http://127.0.0.1:5001", // È†êÂÖàÂÆöÁæ©ÂæåÁ´ØURL
      processing: false,
      progress: 0,
      processStage: "",
      shortcuts: [
        { key: "Enter", function: "ÊèíÂÖ•ÂêåÁ¥öÁØÄÈªû" },
        { key: "Tab", function: "ÊèíÂÖ•Â≠êÁØÄÈªû" },
        { key: "F1", function: "Â∞áÂ∞éÂúñÁΩÆ‰∏≠" },
        { key: "F2", function: "Á∑®ËºØÁï∂ÂâçÁØÄÈªû" },
        { key: "‚Üë", function: "ÈÅ∏Êìá‰∏ä‰∏ÄÂÄãÂêåÁ¥öÁØÄÈªû" },
        { key: "‚Üì", function: "ÈÅ∏Êìá‰∏ã‰∏ÄÂÄãÂêåÁ¥öÁØÄÈªû" },
        { key: "‚Üê / ‚Üí", function: "ÈÅ∏ÊìáÁà∂ÁØÄÈªû / Á¨¨‰∏ÄÂÄãÂ≠êÁØÄÈªû" },
        { key: "PageUp / Alt + ‚Üë", function: "‰∏äÁßªÁØÄÈªû" },
        { key: "PageDown / Alt + ‚Üì", function: "‰∏ãÁßªÁØÄÈªû" },
        { key: "Ctrl + ‚Üë", function: "‰ΩàÂ±ÄÁÇ∫„ÄåÂÅ¥ÈÇä„Äç" },
        { key: "Ctrl + ‚Üê", function: "‰ΩàÂ±ÄÁÇ∫„ÄåÂ∑¶ÂÅ¥„Äç" },
        { key: "Ctrl + ‚Üí", function: "‰ΩàÂ±ÄÁÇ∫„ÄåÂè≥ÂÅ¥„Äç" },
        { key: "Ctrl + C", function: "Ë§áË£ΩÁï∂ÂâçÁØÄÈªû" },
        { key: "Ctrl + V", function: "Ë≤º‰∏äË§áË£ΩÁöÑÁØÄÈªû" },
        { key: "Ctrl + +", function: "ÊîæÂ§ßÂ∞éÂúñ" },
        { key: "Ctrl + -", function: "Á∏ÆÂ∞èÂ∞éÂúñ" },
        { key: "Ctrl + 0", function: "ÈáçÁΩÆÂ∞éÂúñÁ∏ÆÊîæÁ≠âÁ¥ö" },
      ],
      example: {
        nodeData: {
          id: "root",
          topic: "‰∏≠ÂøÉ‰∏ªÈ°å",
          root: true,
          children: [],
        },
        linkData: {},
        direction: 1,
        template: "default",
      },
    };
  },
  mounted() {
    const options = {
      el: "#map",
      direction: MindElixir.SIDE, // ÊîπÁÇ∫SIDEÔºå‰ΩøÂøÉÊô∫ÂúñÂëàÁèæÂ∑¶Âè≥Â∞çÁ®±
      draggable: true,
      contextMenu: true,
      toolBar: true,
      nodeMenu: true,
      keypress: true,
    };

    this.mind = new MindElixir(options);
    this.mind.init(this.example);
  },
  methods: {
    handleFileChange(event) {
      this.fileName = event.target.files[0].name;
    },
    async uploadFile() {
      // Ê™¢Êü•ÊòØÂê¶ÈÅ∏Êìá‰∫ÜÊñá‰ª∂
      const fileInput = this.$refs.fileInput;
      if (!fileInput.files[0]) {
        this.resultMessage = '<p class="error">Ë´ãÈÅ∏Êìá‰∏ÄÂÄãÊ™îÊ°à</p>';
        return;
      }

      // Ê∫ñÂÇô‰∏äÂÇ≥Êï∏Êìö
      const formData = new FormData();
      formData.append("file", fileInput.files[0]);

      // Ë®≠ÁΩÆËôïÁêÜÁãÄÊÖã
      this.processing = true;
      this.progress = 0;
      this.processStage = "‰∏äÂÇ≥Ê™îÊ°à‰∏≠";
      this.resultMessage = "";

      // ÈñãÂßãÊ®°Êì¨ÈÄ≤Â∫¶
      const startTime = Date.now();
      const minProcessTime = 7500; // ÊúÄÂ∞ëË¶Å7.5Áßí
      const maxProcessTime = 15000; // ÊúÄÂ§ö15Áßí

      // ÈöéÊÆµÊñáÂ≠ó
      const stages = [
        "‰∏äÂÇ≥Ê™îÊ°à‰∏≠",
        "ÂàÜÊûêÊñá‰ª∂‰∏≠",
        "Áî¢ÁîüÂøÉÊô∫Âúñ",
        "ÈÄ≤Ë°åÂ§öÈáçÈ©óË≠â",
        "Áî¢ÁîüÂøÉÊô∫Âúñ",
        "ÈÄ≤Ë°å‰∫åÊ¨°Â§öÈáçÈ©óË≠â",
        "ÂÆåÊàê",
      ];

      // Ë®≠ÁΩÆÈÄ≤Â∫¶Ê¢ùË®àÊôÇÂô®
      const progressTimer = setInterval(() => {
        const elapsed = Date.now() - startTime;

        // Ë®àÁÆóÈÄ≤Â∫¶
        if (elapsed < maxProcessTime) {
          // Èö®Ê©üÂ¢ûÂä†ÈÄ≤Â∫¶Ôºå‰ΩÜ‰∏çË∂ÖÈÅé5%
          const randomIncrement = Math.random() * 5;
          this.progress = Math.min(90, this.progress + randomIncrement); // Ê†πÊìöÈÄ≤Â∫¶Êõ¥Êñ∞ÈöéÊÆµÊñáÂ≠ó
          if (this.progress < 15) {
            this.processStage = stages[0];
          } else if (this.progress < 30) {
            this.processStage = stages[1];
          } else if (this.progress < 45) {
            this.processStage = stages[2];
          } else if (this.progress < 60) {
            this.processStage = stages[3];
          } else if (this.progress < 75 && Math.random() > 0.45) {
            this.processStage = stages[4]; // 45%Ê©üÁéáÈÄ≤ÂÖ•Á¨¨‰∫åÊ¨°ÁîüÊàê
          } else if (this.progress < 85 && Math.random() > 0.78) {
            this.processStage = stages[5]; // 22%Ê©üÁéáÈÄ≤ÂÖ•Á¨¨‰∫åÊ¨°È©óË≠â
          } else if (this.progress >= 85 && this.progress < 100) {
            this.processStage = "Ê≠£Âú®Ê∫ñÂÇô";
          } else if (this.progress >= 100) {
            this.processStage = stages[6];
          }
        }
      }, 300);

      try {
        // ÁôºÈÄÅAPIË´ãÊ±Ç
        const response = await axios.post(
          `${this.backendUrl}/upload`,
          formData,
          { headers: { "Content-Type": "multipart/form-data" } }
        );

        // ËôïÁêÜAPIËøîÂõûÁöÑJSONÊï∏Êìö
        const nodeData = this.parseJsonFromResponse(
          response.data.analysis_result
        );

        // ËôïÁêÜÁØÄÈªûÊ†ºÂºè
        this.transformTextToTopic(nodeData);

        // Á¢∫‰øùËá≥Â∞ëÁ∂ìÈÅéminProcessTimeÂæåÊâçÈ°ØÁ§∫ÁµêÊûú
        const elapsed = Date.now() - startTime;
        if (elapsed < minProcessTime) {
          await new Promise((resolve) =>
            setTimeout(resolve, minProcessTime - elapsed)
          );
        }

        // ÈÄ≤Â∫¶Ë®≠ÁÇ∫100%
        this.progress = 100;
        this.processStage = "ÂÆåÊàê";

        // Êõ¥Êñ∞ÂøÉÊô∫Âúñ
        setTimeout(() => {
          clearInterval(progressTimer);
          this.processing = false;
          this.updateMindMap(nodeData, response.data.filename);
        }, 500);
      } catch (error) {
        // ÂÅúÊ≠¢Ë®àÊôÇÂô®
        clearInterval(progressTimer);
        this.processing = false;

        // ËôïÁêÜÈåØË™§
        this.resultMessage = `<p class="error">‰∏äÂÇ≥Â§±Êïó: ${
          error.response?.data?.error || error.message
        }</p>`;
      }
    },
    transformTextToTopic(node) {
      if (node.text && typeof node.text === "string") {
        node.topic = node.text;
        delete node.text;
      }

      if (Array.isArray(node.text)) {
        node.topic = node.text
          .map((item) =>
            typeof item === "string"
              ? item
              : item.name
              ? `${item.name}Ôºà${item.time}Ôºâ`
              : JSON.stringify(item)
          )
          .join("\n");
        delete node.text;
      }

      if (node.children && node.children.length > 0) {
        node.children.forEach((child) => this.transformTextToTopic(child));
      }
    },
    async exportPng() {
      const blob = await this.mind.exportPng();
      if (!blob) return;
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = "mindmap.png";
      a.click();
      URL.revokeObjectURL(url);
    },

    async exportSvg() {
      const svg = this.mind.exportSvg();
      if (!svg) return;

      // Âª∫Á´ã Blob Áâ©‰ª∂
      const blob = new Blob([svg], { type: "image/svg+xml" });
      const url = URL.createObjectURL(blob);

      // Âª∫Á´ã‰∏ãËºâÈÄ£Áµê
      const a = document.createElement("a");
      a.href = url;
      a.download = "mindmap.svg";
      a.click();
      URL.revokeObjectURL(url);
    },

    exportJson() {
      // Áç≤ÂèñÁï∂ÂâçÂøÉÊô∫ÂúñÁöÑÊï∏Êìö
      const data = this.mind.getData();

      // ËΩâÊèõÁÇ∫ JSON Â≠óÁ¨¶‰∏≤
      const jsonStr = JSON.stringify(data, null, 2);

      // Âª∫Á´ã Blob Áâ©‰ª∂
      const blob = new Blob([jsonStr], { type: "application/json" });
      const url = URL.createObjectURL(blob);

      // Âª∫Á´ã‰∏ãËºâÈÄ£Áµê
      const a = document.createElement("a");
      a.href = url;
      a.download = "mindmap.json";
      a.click();
      URL.revokeObjectURL(url);
    },
    generateQuiz() {
      try {
        // Áç≤ÂèñÁï∂ÂâçÁöÑÂøÉÊô∫ÂúñÊï∏Êìö
        const data = this.mind.getData();

        if (!data || !this.fileName) {
          alert("Ë´ãÂÖà‰∏äÂÇ≥‰∏¶ÁîüÊàêÂøÉÊô∫ÂúñÔºåÂÜçÁî¢ÁîüÊ∏¨È©ó");
          return;
        }

        // Â∞á JSON ËΩâÊèõÁÇ∫Â≠óÁ¨¶‰∏≤‰∏¶ÈÄ≤Ë°å URL Á∑®Á¢º
        const jsonDataStr = encodeURIComponent(JSON.stringify(data));

        // Â∞éËà™Âà∞Ê∏¨È©óÈ†ÅÈù¢ÔºåÂ∞áÊ™îÊ°àÂêçÁ®±Âíå JSON Êï∏Êìö‰ΩúÁÇ∫ÂèÉÊï∏ÂÇ≥ÈÅû
        window.location.href = `/quizpage?filename=${encodeURIComponent(
          this.fileName
        )}&data=${jsonDataStr}`;
      } catch (error) {
        console.error("Áî¢ÁîüÊ∏¨È©óÊôÇÁôºÁîüÈåØË™§:", error);
        alert("Áî¢ÁîüÊ∏¨È©óÂ§±ÊïóÔºåË´ãÁ®çÂæåÈáçË©¶");
      }
    },
    parseJsonFromResponse(responseText) {
      let raw = responseText;
      raw = raw.trim(); // ÂÖàÊ∏ÖÁêÜÂâçÂæåÁ©∫ÁôΩ

      if (raw.startsWith("```json")) {
        // ÊâæÂà∞Á¨¨‰∏ÄÂÄãÊèõË°åÂæåÁöÑ‰ΩçÁΩÆÈñãÂßãÔºå‰∏ÄÁõ¥Âà∞ÊúÄÂæåÁöÑ ``` ‰πãÂâç
        raw = raw.substring(raw.indexOf("\n") + 1);
        if (raw.lastIndexOf("```") !== -1) {
          raw = raw.substring(0, raw.lastIndexOf("```"));
        }
      }

      try {
        raw = raw.trim(); // ÂÜçÊ¨°Ê∏ÖÁêÜÂèØËÉΩÁöÑÂâçÂæåÁ©∫ÁôΩ
        return JSON.parse(raw);
      } catch (error) {
        console.error("JSON Ëß£ÊûêÈåØË™§:", error);
        console.log("ÂéüÂßãÊï∏Êìö:", raw);
        this.resultMessage = `<p class="error">JSON Ëß£ÊûêÂ§±Êïó: ${error.message}</p>`;
        throw error;
      }
    },
    updateMindMap(nodeData, filename) {
      this.resultMessage = `<p class="success">Ê™îÊ°à <strong>${filename}</strong> ‰∏äÂÇ≥ÊàêÂäüÔºÅ</p>`;

      this.mind.init({
        nodeData,
        linkData: {},
        direction: 2, // ‰ΩøÁî®2‰ª£Ë°®SIDEÊñπÂêëÔºåÁ¢∫‰øùËàáÂàùÂßãÂåñ‰∏ÄËá¥
        template: "default",
      });
    },
  },
};
</script>

<style scoped>
.mind-map-app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.mind-map-row {
  display: flex;
  flex-wrap: wrap;
}

