  min-height: 400px;
  touch-action: manipulation;
  -webkit-overflow-scrolling: touch; /* Â¢ûÂä†iOSÂπ≥Âè∞ÁöÑÊªëÂãïÊµÅÊö¢Â∫¶ */
}

@media (min-width: 1200px) {
  .mind-map-container {
    min-height: 500px;
  }
}

@media (max-width: 768px) {
  .mind-map-container {
    min-height: 300px;
    aspect-ratio: auto;
    max-height: 60vh; /* ÈôêÂà∂Âú®ÊâãÊ©ü‰∏äÁöÑÈ´òÂ∫¶ */
  }
}

@media (max-width: 600px) {
  .mind-map-container {
    min-height: 200px;
    max-height: 50vh; /* Âú®Â∞èÊâãÊ©ü‰∏äÈÄ≤‰∏ÄÊ≠•Èôç‰ΩéÈ´òÂ∫¶ */
  }
}

.shortcut-section {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  font-size: 14px;
  overflow-y: auto;
}

@media (min-width: 960px) {
  .shortcut-section {
    height: 100%;
    min-height: 500px;
  }
}

@media (max-width: 768px) {
  .shortcut-section {
    max-height: none;
    min-height: auto;
    overflow-y: hidden;
  }
}

.shortcut-scroll {
  overflow-x: hidden;
  flex-grow: 1;
}

@media (max-width: 768px) {
  .shortcut-scroll {
    overflow-y: hidden;
  }
}

.shortcut-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 8px;
}

.shortcut-key {
  width: 70px;
  font-family: monospace;
  white-space: nowrap;
  padding-right: 10px;
  color: #374151;
}

.shortcut-func {
  color: #1f2937;
}

.process-status {
  margin-bottom: 12px;
}

.process-text {
  margin-bottom: 5px;
  font-weight: 500;
  color: #4b5563;
}

.pdf-upload-section {
  background-color: #fff3e0; /* Ê∑∫Ê©òËâ≤ËÉåÊôØ */
  border: 1px solid #ffe0b2;
  transition: all 0.3s ease;
}

.upload-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-container {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
}

.upload-btn {
  min-width: 120px;
}

@media (max-width: 600px) {
  .upload-btn {
    width: 100%;
    margin-left: 0 !important;
  }
}

.export-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.export-btn {
  min-width: 80px;
}

@media (max-width: 768px) {
  .quiz-btn {
    margin-top: 8px;
    width: 100%;
  }
}

.success {
  color: green;
}
.error {
  color: red;
}
</style>
