            Ê≠£Âú®ÁÇ∫ÊÇ®ÁîüÊàêÊ∏¨È©óÈ°åÁõÆÔºåË´ãÁ®çÂÄô...
          </p>
          <p class="text-body-2 text-grey mt-2">
            Ê†πÊìöÊñá‰ª∂Â§ßÂ∞èÂíåË§áÈõúÂ∫¶ÔºåÈÄôÂèØËÉΩÈúÄË¶ÅÂπæÁßíÈêòÁöÑÊôÇÈñì„ÄÇ
          </p>
          <div class="loading-tips mt-4 mt-md-8 pa-3 pa-md-4">
            <p class="text-caption font-italic">
              ÊèêÁ§∫ÔºöÊ∏¨È©óÂÆåÊàêÂæåÔºåÊÇ®ÂèØ‰ª•ÁúãÂà∞ÊÇ®ÁöÑÁ≠îÈ°åÂæóÂàÜÁéáÔºåÂπ´Âä©ÊÇ®Ë©ï‰º∞Â∞çÂÖßÂÆπÁöÑÁêÜËß£Á®ãÂ∫¶„ÄÇ
            </p>
