      </v-card>
    </div>
    <!-- Ê≠°Ëøé‰ø°ÊÅØ - ÂΩìÊ≤°ÊúâÈóÆÈ¢òÊï∞ÊçÆ‰∏î‰∏çÂú®Âä†ËºâÁãÄÊÖãÊôÇÈ°ØÁ§∫ -->
    <div
      v-if="questions.length === 0 && !isLoading"
      class="no-data-container mx-auto my-5 my-md-10 text-center"
    >
      <v-card
        class="pa-4 pa-md-6 elevation-3 mx-auto"
        max-width="95%"
        style="max-width: 500px; width: 100%"
      >
        <v-card-title class="text-h5 text-md-h4 mb-2 mb-md-4"
          >Ê≠°Ëøé‰æÜÂà∞Ê∏¨È©óÈ†ÅÈù¢</v-card-title
        >
        <v-card-text>
          <p class="text-subtitle-2 text-md-subtitle-1 mb-3 mb-md-6">
            ÊÇ®‰ºº‰πéÊòØÁõ¥Êé•Ë®™ÂïèÊ≠§È†ÅÈù¢ÔºåËÄåÊ≤íÊúâÂæûÂøÉÊô∫ÂúñÈ†ÅÈù¢Ë∑≥ËΩâÈÅé‰æÜ„ÄÇ
          </p>
          <p class="mb-4 mb-md-8">
            ÁÇ∫‰∫ÜÁç≤ÂæóÊ∏¨È©óÈ°åÁõÆÔºåË´ãÂÖàÂâçÂæÄÂøÉÊô∫ÂúñÈ†ÅÈù¢‰∏äÂÇ≥ PDF Êñá‰ª∂‰∏¶ÁîüÊàêÂøÉÊô∫ÂúñÔºå
            ÁÑ∂ÂæåÈªûÊìä„ÄåÁîüÊàêÊ∏¨È©ó„ÄçÊåâÈàï‰æÜÂâµÂª∫ÈáùÂ∞çË©≤ÂÖßÂÆπÁöÑÊ∏¨È©óÂïèÈ°å„ÄÇ
          </p>
          <v-btn
            color="primary"
            size="large"
            href="/mindmap"
            class="mt-2 mt-md-4"
          >
            ÂâçÂæÄÂøÉÊô∫ÂúñÈ†ÅÈù¢
          </v-btn>
        </v-card-text>
      </v-card>
    </div>
    <!-- ÊµãÈ™åÂÜÖÂÆπ - ÂΩìÊúâÈóÆÈ¢òÊï∞ÊçÆ‰∏îÊúâÂΩìÂâçÈóÆÈ¢ò‰∏î‰∏çÂú®Âä†ËºâÁãÄÊÖãÊôÇÈ°ØÁ§∫ -->
    <div
      class="quiz-container mx-auto px-2 px-md-3 pt-2 overflow-hidden"
      v-if="questions.length > 0 && currentQuestion && !isLoading"
    >
      <!-- Ê®ôÈ°å -->
      <div class="text-center mb-3 mb-md-4 mt-4 mt-md-6">
        <h2 class="text-h4 text-md-h3 font-bold text-primary mb-2">Â∞èÊ∏¨È©ó</h2>
      </div>
      <!-- È°åÁõÆÂç°Áâá -->
      <v-card
        class="pa-3 pa-sm-4 pa-md-6 elevation-3 mx-auto question-card"
        max-width="95%"
      >
        <v-card-text>
          <div v-if="!quizFinished">
            <!-- È°ØÁ§∫ÁõÆÂâçÈ°åÁõÆ -->
            <h3
              class="text-subtitle-1 text-md-h6 font-weight-bold mb-3 mb-md-6"
            >
              {{ currentQuestionIndex + 1 }}. {{ currentQuestion.question }}
            </h3>

            <!-- ÂñÆÈÅ∏È°åËôïÁêÜ -->
            <div
              v-if="currentQuestion.type === 'single'"
              class="d-flex flex-column gap-3"
            >
              <v-btn
                v-for="(option, index) in currentQuestion.options"
                :key="`${index}-${currentQuestionIndex}`"
                class="justify-start text-left option-btn"
                variant="outlined"
                :disabled="showResult"
                :class="{
                  'text-grey':
                    showResult &&
                    index !== selectedAnswer &&
                    index !== currentQuestion.correct,
                  'text-error':
                    showResult &&
                    index === selectedAnswer &&
                    index !== currentQuestion.correct,
                  'text-success':
                    showResult && index === currentQuestion.correct,
                }"
                @click="handleSelectAnswer(index)"
              >
                <span class="option-text">{{ option }}</span>
                <v-icon
                  v-if="showResult && index === currentQuestion.correct"
                  color="green"
                  class="ml-2 result-icon"
                  >mdi-check</v-icon
                >
                <v-icon
                  v-if="
                    showResult &&
                    index === selectedAnswer &&
                    index !== currentQuestion.correct
                  "
                  color="red"
                  class="ml-2 result-icon"
                  >mdi-close</v-icon
                >
              </v-btn>
            </div>

            <!-- ÊòØÈùûÈ°åËôïÁêÜ -->
            <div
              v-else-if="currentQuestion.type === 'true-false'"
              class="d-flex flex-column gap-3"
            >
              <v-btn
                class="justify-start text-left option-btn"
                variant="outlined"
                :disabled="showResult"
                :class="{
                  'text-grey':
                    showResult &&
                    selectedAnswer !== null &&
                    selectedAnswer !== true &&
                    currentQuestion.correct !== true,
                  'text-success':
                    showResult && currentQuestion.correct === true,
                  'text-error':
                    showResult &&
                    selectedAnswer === true &&
                    currentQuestion.correct !== true,
                }"
                @click="handleSelectAnswer(true)"
              >
                <span class="option-text">ê§è</span
                ><!-- ÂãæÂãæÔºöÁ≠îÂ∞ç -->
                <v-icon
                  v-if="showResult && currentQuestion.correct === true"
                  color="green"
                  class="ml-2 result-icon"
                  >mdi-check</v-icon
                >
                <!-- ÂèâÂèâÔºöË™§ÈÅ∏ -->
                <v-icon
                  v-if="
                    showResult &&
                    selectedAnswer === true &&
                    currentQuestion.correct !== true
                  "
                  color="red"
                  class="ml-2 result-icon"
                  >mdi-close</v-icon
                >
              </v-btn>

              <v-btn
                class="justify-start text-left option-btn"
                variant="outlined"
                :disabled="showResult"
                :class="{
                  'text-grey':
                    showResult &&
                    selectedAnswer !== null &&
                    selectedAnswer !== false &&
                    currentQuestion.correct !== false,
                  'text-success':
                    showResult && currentQuestion.correct === false,
                  'text-error':
                    showResult &&
                    selectedAnswer === false &&
                    currentQuestion.correct !== false,
                }"
                @click="handleSelectAnswer(false)"
              >
                <span class="option-text">‚úï</span
                ><!-- ÂãæÂãæÔºöÁ≠îÂ∞ç -->
                <v-icon
                  v-if="showResult && currentQuestion.correct === false"
                  color="green"
                  class="ml-2 result-icon"
                  >mdi-check</v-icon
                >
                <!-- ÂèâÂèâÔºöË™§ÈÅ∏ -->
                <v-icon
                  v-if="
                    showResult &&
                    selectedAnswer === false &&
                    currentQuestion.correct !== false
                  "
                  color="red"
                  class="ml-2 result-icon"
                  >mdi-close</v-icon
                >
              </v-btn>
            </div>
            <!-- Á≠îÈ°åÁµêÊûúÈ°ØÁ§∫ -->
            <div v-if="showResult" class="text-center mt-4 mt-md-6">
              <p
                v-if="selectedAnswer === currentQuestion.correct"
                class="text-success font-weight-medium"
              >
                ÂõûÁ≠îÊ≠£Á¢∫ÔºÅ
              </p>
              <p v-else class="text-error font-weight-medium">ÂõûÁ≠îÈåØË™§ÔºÅ</p>
              <v-btn class="mt-3 mt-md-4" color="primary" @click="nextQuestion"
                >‰∏ã‰∏ÄÈ°å</v-btn
              >
            </div>
          </div>
          <!-- Ê∏¨È©óÂÆåÊàêÁï´Èù¢ -->
          <div v-else class="text-center">
            <h3 class="text-h6 text-md-h5 font-weight-bold mb-3 mb-md-4">
              üéâ Ê∏¨È©óÂÆåÊàê
            </h3>
            <p class="text-subtitle-2 text-md-subtitle-1 mb-2">
              ‰Ω†Á≠îÂ∞ç‰∫Ü {{ correctCount }} / {{ questions.length }} È°å
            </p>
            <p class="text-grey">
              ÂæóÂàÜÁéáÔºö{{ Math.round((correctCount / questions.length) * 100) }}%
            </p>
            <div
              class="d-flex flex-column flex-sm-row justify-center mt-4 mt-md-6 button-container"
            >
              <v-btn
                color="indigo"
                class="mb-3 mb-sm-0 mx-sm-3"
                @click="restartQuiz"
                >ÈáçÊñ∞ÈñãÂßã</v-btn
              >
              <v-btn color="success" class="mx-sm-3" @click="regenerateQuiz"
                >ÈáçÊñ∞ÁîüÊàêÊñ∞È°åÁõÆ</v-btn
              >
            </div>
          </div>
        </v-card-text>
      </v-card>
      <!-- ‰∏ãÊñπÈÄ≤Â∫¶Ê¢ù -->
      <v-card
        class="mt-4 mt-md-6 py-2 progress-card"
        color="grey-lighten-3"
        flat
        tile
      >
        <v-card-text class="px-1 px-sm-2">
          <div class="text-center text-sm font-bold mb-2">
            {{ answeredCount }} / {{ questions.length }}
          </div>
          <div
            class="d-flex flex-column flex-sm-row align-center justify-space-between px-2 px-md-4 progress-container"
          >
            <span
              class="text-error font-weight-medium mb-2 mb-sm-0 progress-text"
            >
              ÈåØË™§Ôºö{{ incorrectCount }}
            </span>
            <v-progress-linear
              :key="progressPercentage"
              :model-value="progressPercentage"
              color="blue"
              height="10"
              rounded
              class="progress-bar mx-auto mx-sm-0"
            ></v-progress-linear>
            <span
              class="text-success font-weight-medium mt-2 mt-sm-0 progress-text"
            >
              Ê≠£Á¢∫Ôºö{{ correctCount }}
            </span>
          </div>
        </v-card-text>
      </v-card>
    </div>

    <Footer />
  </div>
</template>

<script setup>
// Vue Composition API
import { ref, computed, onMounted } from "vue";
import axios from "axios";
import Header from "@/views/AppHeader.vue";
import Footer from "@/views/AppFooter.vue";

// ÁãÄÊÖãËÆäÊï∏
const questions = ref([]);
const currentQuestionIndex = ref(0);
const selectedAnswer = ref(null);
const showResult = ref(false);
const correctCount = ref(0);
const incorrectCount = ref(0);
const answeredCount = ref(0);
const quizFinished = ref(false);
const isLoading = ref(false); // Âä†ËºâÁãÄÊÖã

// Ë®àÁÆóÁõÆÂâçÈ°åÁõÆËàáÈÄ≤Â∫¶Ê¢ù
const currentQuestion = computed(
  () => questions.value[currentQuestionIndex.value]
);
const progressPercentage = computed(() =>
  Math.round((answeredCount.value / questions.value.length) * 100)
);

// ÂæûAPIÁç≤ÂèñÂïèÈ°å
async function fetchQuestions(filename = null, jsonData = null) {
  try {
    // Â¶ÇÊûúÊ≤íÊúâÊèê‰æõÊñá‰ª∂ÂêçÊàñÊï∏ÊìöÔºåÂâá‰∏çÁôºÈÄÅË´ãÊ±ÇÔºå‰øùÊåÅÁ©∫ÂïèÈ°åÂàóË°®‰ª•È°ØÁ§∫Ê≠°ËøéÈ†ÅÈù¢
    if (!filename && (!jsonData || Object.keys(jsonData).length === 0)) {
      console.log("Êú™Êèê‰æõÊñá‰ª∂ÂêçÊàñÂøÉÊô∫ÂúñÊï∏ÊìöÔºåÈ°ØÁ§∫Ê≠°ËøéÈ†ÅÈù¢");
      return;
    }

    // Ë®≠ÁΩÆÂä†ËºâÁãÄÊÖãÁÇ∫ true
    isLoading.value = true;

    // Ê∫ñÂÇôË´ãÊ±ÇÊï∏Êìö
    const requestData = {
      filename: filename || "",
      mindMapData: jsonData || {},
    };

    // ‰ΩøÁî® POST ËÄå‰∏çÊòØ GETÔºå‰∏¶ÁôºÈÄÅÊñá‰ª∂ÂêçÂíåJSONÊï∏Êìö
    const response = await axios.post("/api/quiz/questions", requestData);

    console.log("ÈÄôÊòØresponse", response);

    if (
      response.data.success &&
      response.data.questions &&
      response.data.questions.length > 0
    ) {
      questions.value = response.data.questions;
    } else {
      console.error("Áç≤ÂèñÂïèÈ°åÂ§±Êïó:", response.data.error || "Ê≤íÊúâËøîÂõûÂïèÈ°å");
      // Â¶ÇÊûúÊ≤íÊúâÂïèÈ°åËøîÂõûÔºå‰øùÊåÅÂïèÈ°åÂàóË°®ÁÇ∫Á©∫ÔºåÈÄôÊ®£ÊúÉÈ°ØÁ§∫Ê≠°ËøéÈ†ÅÈù¢
    }
  } catch (error) {
    console.error("APIË´ãÊ±ÇÈåØË™§:", error);
    if (error.code === "ERR_CONNECTION_REFUSED") {
      alert("ÁÑ°Ê≥ïÈÄ£Êé•Âà∞ÂæåÁ´ØÊúçÂãôÂô®ÔºåË´ãÁ¢∫‰øùÂæåÁ´ØÊúçÂãôÂô®Ê≠£Âú®ÈÅãË°å");
    } else {
      alert("Áç≤ÂèñÂïèÈ°åÂ§±ÊïóÔºåË´ãÁ®çÂæåÈáçË©¶");
    }
  } finally {
    // ÁÑ°Ë´ñÊàêÂäüÊàñÂ§±ÊïóÔºåÈÉΩË®≠ÁΩÆÂä†ËºâÁãÄÊÖãÁÇ∫ false
    isLoading.value = false;
  }
}

// ÂàùÂßãÂåñÊ∏¨È©ó
async function initQuiz() {
  // Ê™¢Êü•ÊòØÂê¶ÊúâÂæûË∑ØÁî±ÂÇ≥ÈÅûÈÅé‰æÜÁöÑÂèÉÊï∏
  const params = new URLSearchParams(window.location.search);
  const filename = params.get("filename");
  const jsonData = params.get("data");

  let parsedData = null;
  if (jsonData) {
    try {
      parsedData = JSON.parse(decodeURIComponent(jsonData));
    } catch (e) {
      console.error("Ëß£ÊûêÂøÉÊô∫ÂúñÊï∏ÊìöÂ§±Êïó:", e);
    }
  } // Ê™¢Êü•ÊòØÂê¶ÂæûÂøÉÊô∫ÂúñÈ†ÅÈù¢Ê≠£Á¢∫Ë∑≥ËΩâÈÅé‰æÜ
  const hasRequiredParams = filename || (jsonData && parsedData);

  if (hasRequiredParams) {
    await fetchQuestions(filename, parsedData);
  } else {
    console.log("Áõ¥Êé•Ë®™ÂïèÊ∏¨È©óÈ†ÅÈù¢ÔºåÈ°ØÁ§∫Ê≠°Ëøé‰ø°ÊÅØ");
    // ‰øùÊåÅ questions ÁÇ∫Á©∫Êï∏ÁµÑÔºåÈÄôÊ®£ÊúÉÈ°ØÁ§∫Ê≠°ËøéÈ†ÅÈù¢
  }
}

// ‰ΩøÁî®ËÄÖÈªûÊìäÈÅ∏È†ÖÂæåÁöÑËôïÁêÜ
function handleSelectAnswer(index) {
  if (!showResult.value) {
    selectedAnswer.value = index;
    showResult.value = true;
    const isCorrect = index === currentQuestion.value.correct;
    if (isCorrect) correctCount.value++;
    else incorrectCount.value++;
    answeredCount.value++;
  }
}

// ÂàáÊèõ‰∏ã‰∏ÄÈ°å
function nextQuestion() {
  if (currentQuestionIndex.value < questions.value.length - 1) {
    currentQuestionIndex.value++;
    selectedAnswer.value = null;
    showResult.value = false;
  } else {
    quizFinished.value = true;
  }
}

// ÈáçÊñ∞ÈñãÂßãÊ∏¨È©ó - ‰ΩøÁî®ÁèæÊúâÈ°åÁõÆ
function restartQuiz() {
  currentQuestionIndex.value = 0;
  selectedAnswer.value = null;
  showResult.value = false;
  correctCount.value = 0;
  incorrectCount.value = 0;
  answeredCount.value = 0;
  quizFinished.value = false;
  // ‰∏çÂëºÂè´ initQuizÔºåÁõ¥Êé•ÈáçÁî®ÁèæÊúâÈ°åÁõÆ
}

// ÈáçÊñ∞ÁîüÊàêÊñ∞È°åÁõÆ
async function regenerateQuiz() {
  currentQuestionIndex.value = 0;
  selectedAnswer.value = null;
  showResult.value = false;
  correctCount.value = 0;
  incorrectCount.value = 0;
  answeredCount.value = 0;
  quizFinished.value = false;

  // Ê™¢Êü•ÊòØÂê¶ÊúâÂæûË∑ØÁî±ÂÇ≥ÈÅûÈÅé‰æÜÁöÑÂèÉÊï∏
  const params = new URLSearchParams(window.location.search);
  const filename = params.get("filename");
  const jsonData = params.get("data");

  let parsedData = null;
  if (jsonData) {
    try {
      parsedData = JSON.parse(decodeURIComponent(jsonData));
    } catch (e) {
      console.error("Ëß£ÊûêÂøÉÊô∫ÂúñÊï∏ÊìöÂ§±Êïó:", e);
    }
  }

  // ÈáçÊñ∞Áç≤ÂèñÈ°åÁõÆ
  await fetchQuestions(filename, parsedData);
}

// ÂàùÂßãÂåñÊôÇÂü∑Ë°å‰∏ÄÊ¨°
onMounted(() => {
  initQuiz();
});
</script>

<style scoped>
/* ÂèØÂä†‰∏äËÉåÊôØÊ®£Âºè */
body {
  background-color: #f5f7fa;
  overflow-x: hidden; /* Èò≤Ê≠¢Ê∞¥Âπ≥Êç≤Âãï */
  width: 100%;
  max-width: 100vw;
}

.quiz-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden; /* Èò≤Ê≠¢Ê∞¥Âπ≥Êç≤Âãï */
}

.quiz-container {
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden; /* Èò≤Ê≠¢Ê∞¥Âπ≥Êç≤Âãï */
}

.no-data-container,
.loading-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 1rem;
}

.no-data-container .v-card,
.loading-container .v-card {
  transition: all 0.3s ease;
  border-radius: 12px;
}

.no-data-container .v-card:hover,
.loading-container .v-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15);
  transform: translateY(-3px);
}

/* Êñ∞Â¢ûÔºöÁµ±‰∏ÄÈÅ∏È†ÖÊåâÈàïÊ®£Âºè */
.v-btn.option-btn {
  min-height: 50px !important; /* ËºÉÂ∞èË®≠ÂÇôÁöÑÈ´òÂ∫¶ */
  height: auto !important; /* ÂÖÅË®±Ëá™ÈÅ©ÊáâÈ´òÂ∫¶ */
  width: 100%;
  max-width: 100%;
  white-space: normal;
  text-align: left;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 12px;
  overflow: hidden;
  position: relative; /* ÁÇ∫ÁµïÂ∞çÂÆö‰ΩçÁöÑÂúñÊ®ôÊèê‰æõÂèÉËÄÉ */
  overflow-wrap: break-word;
  word-break: break-word;
  box-sizing: border-box;
}

@media (min-width: 600px) {
  .v-btn.option-btn {
    min-height: 60px !important; /* Â§ßÂ±èÂπï‰ΩøÁî®Êõ¥È´òÁöÑÈ´òÂ∫¶ */
    padding: 12px 16px;
  }
}

/* Á¢∫‰øùÊñáÊú¨Âú®ÊåâÈàïÂÖßÈÉ®Ê≠£Á¢∫ÊèõË°å */
.option-text {
  display: block;
